import { Input } from "antd";

export const Window = () =>{
    return(window.location.pathname)
}

export const CustomInput = (props) =>{
    const {onChange} = props;
    var regex = /^[A-Za-z]*$/;
    let validate = [{mode:"string",regex:/^[A-Za-z ]*$/},{mode:"integer",regex:/^[0-9]*$/}];
    if(validate.length>0){
        validate.map((data)=>{
            if(data.mode === props.mode){
                regex = data.regex;
            }
            return regex
        })
    }
    
    const handle_change = (e) =>{
        var value = e.target.value;
        if(value === "" || regex.test(value)){
            onChange(value);
        }
    }
    
    return(
        <Input {...props} onChange={handle_change}/>
    )
}
